<template>
  <div class="nursingplanlist">
    <NursingplanPrintOption ref="enterEdit"></NursingplanPrintOption>

    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>護理計畫單</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd :viewData="viewData" @getAllData="getAllData" ref="editProcess"></popupadd>
              <v-btn class="primary mx-1" @click="print">列印</v-btn>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th rowspan="3"></th>
                        <th colspan="7">護理計畫單</th>
                        <th rowspan="2" class="ced_man_key">紀錄人</th>
                        <th rowspan="2" class="ced_man_key">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>評值日期</th>
                        <th>停止日期</th>
                        <th>護理診斷</th>
                        <th>可能導因</th>
                        <th>護理目標</th>
                        <th>護理措施</th>
                        <th>評值說明</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click.stop="enterNursingRecord(item)">
                                <v-list-item-title>轉入護理紀錄</v-list-item-title>
                              </v-list-item> -->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.stop_date}}</td>
                        <td>{{item.question}}</td>
                        <td>{{item.relevant}}</td>
                        <td>{{item.target}}</td>
                        <td>{{item.measures}}</td>
                        <td>{{item.plan}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import dayjs from 'dayjs';
import popupadd from "@/views/document/NursingplanAdd";
import NursingplanPrintOption from "@/components/NursingplanPrintOption";

export default {
  components: { popupadd , NursingplanPrintOption },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/nursingplan",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data.map(i=>{
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.create_man = i.create_man;
            newItem.edit_man = i.edit_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'nursingplan',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/nursingplan",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    //建構護理紀錄內容:
    enterNursingRecord(item) {
      var NursingRecordData = {
        date: item.date,
        u_snkey: item.u_snkey,
        nursingrecord_content:
          "[護理計畫單內容]" +
          "\n" +
          "護理診斷:" +
          "\n" +
          item.question +
          "\n\n" +
          "可能導因:" +
          "\n" +
          item.relevant +
          "\n\n" +
          "護理目標:" +
          "\n" +
          item.target +
          "\n\n" +
          "護理措施:" +
          "\n" +
          item.measures +
          "\n\n" +
          "評值說明:" +
          "\n" +
          item.plan
      };

      //判斷護理紀錄資料是否存在
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };
      //取得該病歷所有護理紀錄資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingrecord",
          qs.stringify(postData)
        )
        .then(rs => {
          var matchData = rs.data.filter(i => {
            return i.date == item.date;
          });

          this.$store.commit("getTime"); //取得現在時間
          if (matchData.length == 0) {
            //新增
            NursingRecordData["create_man"] =
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")"; //執行動作的人,時間,內容

            this.axios
              .post(
                "general/add/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(NursingRecordData)
              )
              .then(rs => {
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄-已新增",
                    type: true,
                    theme: "success"
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          } else {
            //修改
            NursingRecordData["snkey"] = matchData[0].snkey;
            NursingRecordData["nursingrecord_content"] =
              matchData[0].nursingrecord_content +
              NursingRecordData.nursingrecord_content;
            NursingRecordData["edit_man"] =
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")"; //執行動作的人,時間,內容

            this.axios
              .post(
                "general/edit/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(NursingRecordData)
              )
              .then(rs => {
                console.log(rs.data);
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄資料-已追加",
                    type: true,
                    theme: "success"
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    print() {
      this.$refs.enterEdit.$emit("childFn", this.items);
    }
  }
};
</script>


<style lang="scss">
.nursingplanlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>