<template>
  <div class="nursingrecordlistdatalist">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="white" dark large v-on="on" @click="enterProcess"
          >mdi-cog-box</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>
          {{ title }}
          <v-spacer></v-spacer>
          <v-icon @click="dialog = false">mdi-close</v-icon>
        </v-card-title>

        <v-card-text>
          <v-row>
            <!-- 問題 -->
            <v-col cols="12" md="3">
              <v-form ref="form" class="mt-3">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      prepend-icon="mdi-star"
                      label="新增問題 or 搜尋"
                      v-model="list.question"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn class="primary" block @click="addOK('question')"
                      >確認新增</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>

              <v-card class="my-2">
                <v-card-text>
                  <v-sheet
                    color="green lighten-3"
                    outlined
                    class="text-center pa-2"
                  >
                    <h3 class="white--text">已建構問題列表</h3>
                  </v-sheet>
                  <v-list>
                    <v-list-item-group v-model="model" color="red">
                      <v-list-item
                        v-for="item in questionfilter"
                        :key="item.snkey"
                        @click.stop="questionData = item"
                      >
                        <v-list-item-content>
                          {{ item.unit }}
                        </v-list-item-content>
                        <v-icon color="red" @click.stop="delQuestion(item)"
                          >mdi-close</v-icon
                        >
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>

            <!-- 相關因素 -->
            <v-col cols="12" md="3">
              <v-form ref="form" class="mt-3">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      prepend-icon="mdi-format-list-bulleted"
                      label="新增相關因素"
                      v-model="list.relevant"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn class="primary" block @click="addOK('relevant')"
                      >確認新增</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>

              <v-card class="my-2">
                <v-card-text>
                  <v-sheet
                    color="green lighten-3"
                    outlined
                    class="text-center pa-2"
                  >
                    <h3 class="white--text">已建構相關因素</h3>
                  </v-sheet>
                  <v-list>
                    <v-list-item-group>
                      <v-list-item v-for="(item, i) in relevantfilter" :key="i">
                        <v-list-item-content>
                          <!-- <v-list-item-title v-text="item.unit"></v-list-item-title> -->
                          {{ item.unit }}
                        </v-list-item-content>
                        <v-icon color="red" @click.stop="del(item)"
                          >mdi-close</v-icon
                        >
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>

            <!-- 目標 -->
            <v-col cols="12" md="3">
              <v-form ref="form" class="mt-3">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      prepend-icon="mdi-format-list-bulleted"
                      label="新增目標"
                      v-model="list.target"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn class="primary" block @click="addOK('target')"
                      >確認新增</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>

              <v-card class="my-2">
                <v-card-text>
                  <v-sheet
                    color="green lighten-3"
                    outlined
                    class="text-center pa-2"
                  >
                    <h3 class="white--text">已建構目標</h3>
                  </v-sheet>
                  <v-list>
                    <v-list-item-group>
                      <v-list-item v-for="(item, i) in targetfilter" :key="i">
                        <v-list-item-content>
                          <!-- <v-list-item-title v-text="item.unit"></v-list-item-title> -->
                          {{ item.unit }}
                        </v-list-item-content>
                        <v-icon color="red" @click.stop="del(item)"
                          >mdi-close</v-icon
                        >
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>

            <!-- 措施 -->
            <v-col cols="12" md="3">
              <v-form ref="form" class="mt-3">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      prepend-icon="mdi-format-list-bulleted"
                      label="新增措施"
                      v-model="list.measures"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn class="primary" block @click="addOK('measures')"
                      >確認新增</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>

              <v-card class="my-2">
                <v-card-text>
                  <v-sheet
                    color="green lighten-3"
                    outlined
                    class="text-center pa-2"
                  >
                    <h3 class="white--text">已建構措施</h3>
                  </v-sheet>
                  <v-list>
                    <v-list-item-group>
                      <v-list-item v-for="(item, i) in measuresfilter" :key="i">
                        <v-list-item-content>
                          <!-- <v-list-item-title v-text="item.unit"></v-list-item-title> -->
                          {{ item.unit }}
                        </v-list-item-content>
                        <v-icon color="red" @click.stop="del(item)"
                          >mdi-close</v-icon
                        >
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  props: ["nursingrecordlistItems"],

  data() {
    return {
      list: {
        question: "",
        relevant: "",
        target: "",
        measures: "",
      }, //存放表單資料
      dialog: false,
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      model: 0, //判斷是否有選擇question及選了第幾個index
      questionData: "",
    };
  },

  computed: {
    questionfilter() {
      var str = "";
      let matchData = this.nursingrecordlistItems.filter((item) => {
        str = JSON.stringify(item).toUpperCase();
        if (
          str.includes(this.list.question.toUpperCase()) &&
          item.p_snkey == 0
        ) {
          return item;
        }
      });
      return this.sort(matchData);
    },
    relevantfilter() {
      let matchData = this.nursingrecordlistItems.filter((item) => {
        return (
          item.p_snkey == this.questionData.snkey && item.state == "relevant"
        );
      });
      return this.sort(matchData);
    },
    targetfilter() {
      let matchData = this.nursingrecordlistItems.filter((item) => {
        return (
          item.p_snkey == this.questionData.snkey && item.state == "target"
        );
      });
      return this.sort(matchData);
    },
    measuresfilter() {
      let matchData = this.nursingrecordlistItems.filter((item) => {
        return (
          item.p_snkey == this.questionData.snkey && item.state == "measures"
        );
      });
      return this.sort(matchData);
    },
  },

  methods: {
    //依號碼大小排序
    sort(matchData) {
      return matchData.sort(function (a, b) {
        // return a.unit > b.unit ? 1 : -1;
        let noA = a.unit.split(".");
        let noB = b.unit.split(".");
        if (parseInt(noA[0]) > parseInt(noB[0])) {
          return 1;
        } else {
          return -1;
        }
      });
    },
    //進入畫面時,執行的是新增作業
    enterProcess() {
      this.title = "護理計畫選單資料管理";
      this.titleStyle = "font-weight-black primary lighten-2";
      this.questionData = this.questionfilter[0];
      this.model = 0;
    },
    addOK(state) {
      if (this.list[state].length == 0) {
        var pop = { msg: "未輸入資料!!", type: true, theme: "error" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      //資料建構
      //判斷p_snkey的值
      var p_snkey = this.questionData["snkey"];
      if (state == "question") {
        p_snkey = 0;
      }

      this.$store.commit("getTime"); //取得現在時間
      var postData = {
        unit: this.list[state],
        p_snkey: p_snkey,
        state: state,
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
      };

      this.axios
        .post(
          "general/add/" +
            this.$store.state.databaseName +
            "/nursing_record_list_data",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "已新增", type: true, theme: "error" };
            this.$store.commit("snackbar", pop);
            this.$emit("getNursingRecordListData");
          }
        });
    },
    delQuestion(delData) {
      //刪除問題->連同相關的各項內容都會刪除
      //刪除功能
      this.$confirm(
        "刪除問題會連同相關因素,目標,措施都一同刪除!!確認刪除?"
      ).then((res) => {
        if (res) {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "nursing_record_list_data",
            info: JSON.stringify(delData),
          };

          this.axios
            .post(
              "nursingrecordlistdata/delQuestionv3/" +
                this.$store.state.databaseName +
                "/nursing_record_list_data",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.$emit("getNursingRecordListData");
              }
            });
        }
      });
    },
    // delQuestion(delData) {
    //   //刪除問題->連同相關的各項內容都會刪除
    //   //刪除功能
    //   this.$confirm(
    //     "刪除問題會連同相關因素,目標,措施都一同刪除!!確認刪除?"
    //   ).then(res => {
    //     if (res) {
    //       this.$store.commit("getTime"); //取得現在時間
    //       var postData = {
    //         snkey: delData.snkey,
    //         info:
    //           this.$store.state.pData.username +
    //           "(" +
    //           this.$store.state.time +
    //           ")(" +
    //           delData.snkey +
    //           ")(" +
    //           delData.unit +
    //           ")"
    //         //刪除的人(刪除時間)(刪除的snkey)(刪除的unit)
    //       };
    //       this.axios
    //         .post(
    //           "nursingrecordlistdata/delQuestion/" +
    //             this.$store.state.databaseName +
    //             "/nursing_record_list_data",
    //           qs.stringify(postData)
    //         )
    //         .then(rs => {
    //           console.log(rs.data);
    //           if (rs.data.state == 1) {
    //             var pop = { msg: "已刪除", type: true, theme: "success" };
    //             this.$store.commit("snackbar", pop);
    //             this.$emit("getNursingRecordListData");
    //           }
    //         });
    //     }
    //   });
    // },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "nursing_record_list_data",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" +
                this.$store.state.databaseName +
                "/nursing_record_list_data",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.$emit("getNursingRecordListData");
              }
            });
        }
      });
    },
    // del(delData) {
    //   //刪除功能
    //   this.$confirm("確認刪除?").then(res => {
    //     if (res) {
    //       this.$store.commit("getTime"); //取得現在時間
    //       var postData = {
    //         snkey: delData.snkey,
    //         info:
    //           this.$store.state.pData.username +
    //           "(" +
    //           this.$store.state.time +
    //           ")(" +
    //           delData.snkey +
    //           ")(" +
    //           delData.unit +
    //           ")"
    //         //刪除的人(刪除時間)(刪除的snkey)(刪除的unit)
    //       };
    //       this.axios
    //         .post(
    //           "general/del/" +
    //             this.$store.state.databaseName +
    //             "/nursing_record_list_data",
    //           qs.stringify(postData)
    //         )
    //         .then(rs => {
    //           // console.log(rs.data);
    //           if (rs.data.state == 1) {
    //             var pop = { msg: "已刪除", type: true, theme: "success" };
    //             this.$store.commit("snackbar", pop);
    //             this.$emit("getNursingRecordListData");
    //           }
    //         });
    //     }
    //   });
    // }
  },
};
</script>

<style lang="scss">
