<template>
  <div class="nursingplanadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-row>
            <v-col cols="12" md="7">
              <v-form ref="form">
                <v-card>
                  <v-card-text>
                    <v-row>
                      <v-col cols="12" md="6">
                        <v-text-field type="date" label="評值說明日期" v-model="list.date" :rules="emptyRules"></v-text-field>
                        <!-- <datepicker :label="'評值說明日期'" v-model="list.date" ></datepicker> -->
                      </v-col>
                      <v-col cols="12" md="6">
                        <v-text-field type="date" label="停止日期" v-model="list.stop_date"></v-text-field>
                        <!-- <datepicker :label="'停止日期'" v-model="list.stop_date"></datepicker> -->
                        <!-- <v-icon @click="list.stop_date = '0000-00-00'">mdi-window-close</v-icon> -->
                      </v-col>
                      <v-col cols="12" class="d-flex align-center red lighten-4 rounded-lg mb-2">
                        <v-select
                          label="已建立護理診斷選擇"
                          :items="questionArr"
                          prepend-icon="mdi-archive-outline"
                          v-model="list.question"
                          hide-details
                          v-if="questionArr.length"
                          @change="setQuestionContent"
                        ></v-select>
                        <v-btn large class="red lighten-4 ml-2 blue--text" @click="list={}">清除所有內容</v-btn>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          label="護理診斷"
                          prepend-icon="mdi-file-document-edit"
                          v-model="list.question"
                          outlined
                          readonly
                          hide-details
                          @click.stop="selectZone='question'"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea
                          label="可能導因"
                          prepend-icon="mdi-file-document-edit"
                          v-model="list.relevant"
                          hide-details
                          @click.stop="selectZone='relevant'"
                        ></v-textarea>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea
                          label="護理目標"
                          prepend-icon="mdi-file-document-edit"
                          v-model="list.target"
                          hide-details
                          @click.stop="selectZone='target'"
                        ></v-textarea>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea
                          label="護理措施"
                          prepend-icon="mdi-file-document-edit"
                          v-model="list.measures"
                          hide-details
                          @click.stop="selectZone='measures'"
                        ></v-textarea>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea
                          label="評值說明"
                          prepend-icon="mdi-file-document-edit"
                          v-model="list.plan"
                          hide-details
                        ></v-textarea>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-form>
            </v-col>

            <v-col cols="12" md="5">
              <!-- 選單區 -->
              <!-- 問題選擇 -->
              <v-card v-if="selectZone == 'question'">
                <v-card-title class="primary white--text">
                  護理診斷選擇:
                  <v-spacer></v-spacer>
                  <NursingrecordlistdataList @getNursingRecordListData="getNursingRecordListData" :nursingrecordlistItems="nursingrecordlistItems"></NursingrecordlistdataList>
                </v-card-title>
                <v-card-text>
                  <v-list>
                    <v-list-item-group v-model="model" color="primary">
                      <v-list-item v-for="(item, i) in questionfilter" :key="i" @click="list.question=item.unit;questionData=item">
                        <v-list-item-icon>
                          <v-icon>mdi-star</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                          <v-list-item-title v-text="item.unit"></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>

              <!-- 可能導因選擇 -->
              <v-card v-if="selectZone == 'relevant'">
                <v-card-title class="green white--text">可能導因選擇: </v-card-title>
                <v-card-text>
                  <v-list>
                    <v-list-item-group color="green">
                      <v-list-item v-for="(item, i) in relevantfilter" :key="i" @click="addContent('relevant',item)">
                        <v-list-item-icon>
                          <v-icon>mdi-playlist-plus</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                          <v-list-item-title v-text="item.unit"></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>

              <!-- 護理目標選擇 -->
              <v-card v-if="selectZone == 'target'">
                <v-card-title class="green white--text">護理目標選擇: </v-card-title>
                <v-card-text>
                  <v-list>
                    <v-list-item-group color="green">
                      <v-list-item v-for="(item, i) in targetfilter" :key="i" @click="addContent('target',item)">
                        <v-list-item-icon>
                          <v-icon>mdi-playlist-plus</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                          <v-list-item-title v-text="item.unit"></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>

              <!-- 護理措施選擇 -->
              <v-card v-if="selectZone == 'measures'">
                <v-card-title class="green white--text">護理措施選擇: </v-card-title>
                <v-card-text>
                  <v-list>
                    <v-list-item-group color="green">
                      <v-list-item v-for="(item, i) in measuresfilter" :key="i" @click="addContent('measures',item)">
                        <v-list-item-icon>
                          <v-icon>mdi-playlist-plus</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                          <v-list-item-title v-text="item.unit"></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import NursingrecordlistdataList from "@/components/NursingrecordlistdataList";
import qs from "qs";

export default {
  components: { NursingrecordlistdataList },

  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      nursingrecordlistItems: [], //這裡存放的是護理計畫選單的資料
      model:"", //選擇護理診斷的index
      questionData: "", 
      selectZone:'question',
      loading:false,
      questionArr:[], //保存已建立的護理診斷種類
    };
  },

  computed: {
    questionfilter() {
      return this.nursingrecordlistItems.filter(item => {
        return item.p_snkey == 0;
      });
    },
    relevantfilter() {
      return this.nursingrecordlistItems.filter(item => {
        return (item.p_snkey == this.questionData.snkey && item.state == 'relevant');
      });  
    },
    targetfilter() {
      return this.nursingrecordlistItems.filter(item => {
        return (item.p_snkey == this.questionData.snkey && item.state == 'target');
      });  
    },
    measuresfilter() {
      return this.nursingrecordlistItems.filter(item => {
        return (item.p_snkey == this.questionData.snkey && item.state == 'measures');
      });  
    },
  },

  props: ["viewData"],

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });

    this.getNursingRecordListData(); //取得護理計畫的資料
  },

  watch: {
    list(val) {
      this.questionfilter.find((item,index)=>{
        if (this.list.question == item.unit){
          this.model = index;
          this.questionData = item;
          this.selectZone = 'question';
        }
      })
    },
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        question:"",
        relevant:"",
        target:"",
        measures:""
      };

      var questionArr = new Set();
      this.viewData.forEach((i) => {
        questionArr.add(i.question);
      });
      this.questionArr = [...questionArr];

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      // if (this.viewData[0] != undefined  ){
      //   this.list = { ...this.viewData[0] };
      // }

      this.list.snkey = "";
      this.list.date = "";
      this.list.stop_date = "";
      this.list.edit_man = "";
      this.selectZone = 'question';
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
      if (this.list.stop_date == "0000-00-00") {
        this.list.stop_date = "";
      }
      
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間

        var postData = {
          u_snkey : this.$store.state.uData.snkey,
          datalist : JSON.stringify(this.list),
          create_man : this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
        }

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/nursingplan",
            qs.stringify(postData)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容
        var postData = {
          snkey : this.list.snkey,
          datalist : JSON.stringify(this.list),
          edit_man : this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
        }

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/nursingplan",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
              for (var i in this.list) {
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }
    },
    getNursingRecordListData() {
      //取得所有護理計畫的資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getAll/" +
            this.$store.state.databaseName +
            "/nursing_record_list_data"
        )
        .then(rs => {
          this.nursingrecordlistItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    addContent(state,item) {  //新增內容到各欄位中
      if (this.list[state] != ''){
        this.list[state] += '\n'  
      }
      this.list[state] += item.unit;
    },
    //設置選擇的護理診斷問題->顯示最後一筆設置的內容
    setQuestionContent(){
      let matchData = this.viewData.filter(i=>{
        return i.question == this.list.question;
      });
      matchData = matchData.sort((a,b)=>{
        return a.date < b.date ? 1: -1;
      });
      this.list = {...matchData[0]};
    },
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>